import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Login from '@/views/Login'
import MyBill from '@/views/MyBill'
import FreeGenerate from '@/views/FreeGenerate'
import ShareFreeGenerate from '@/views/ShareFreeGenerate'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login,
      meta: {
        noCheckSession: true  // 添加该字段，表示进入这个路由是需要登录的
      }
    },
    {
      path: '/MyBill',
      name: 'MyBill',
      component: MyBill
    },
 	{
      path: '/FreeGenerate',
      name: 'FreeGenerate',
      component: FreeGenerate
    },
 	{
      path: '/ShareFreeGenerate',
      name: 'ShareFreeGenerate',
      component: ShareFreeGenerate
    }
  ]
})



