<template>
	<div class="main">
		<img src="../assets/images/banner-login.png" class="img-banner" />
		<div class="form-block">
			<div class="form-group">
				<img src="../assets/images/label-tel.png" class="img-label" />
				<div class="input-box">
					<img src="../assets/images/input-1.png" class="img-input" />
					<input type="number" name="" placeholder="" class="form-input" v-model="mobile" />
				</div>
			</div>
			<div class="form-group">
				<img src="../assets/images/label-card.png" class="img-label" />
				<div class="input-box">
					<img src="../assets/images/input-1.png" class="img-input" />
					<input type="number" name="" placeholder="" class="form-input" v-model="card_num" />
				</div>
			</div>
		</div>
		<div class="btn">
			<img src="../assets/images/btn-1.png" @click="login" />
		</div>
	</div>
</template>
<script>
	import api from '../config/api';
	import { Indicator, Toast } from 'mint-ui';
	export default{
		data(){
			return {
				mobile: '',
				card_num: ''
			}
		},
		methods:{
			login(){
				let self = this;
				if(self.check()){
					Indicator.open();
/*					self.axios.post(api.doLogin()+'&mobile='+self.mobile+'&card_num='+self.card_num)
					  	.then(res => {

				    		var result = res.data;
				    		if(result.status == 1){
				    			Indicator.close();

				    			sessionStorage.setItem('accessToken', result.token);
				    			Toast({message:result.error || '登陆成功', duration: 1500});

				    			setTimeout(function(){
				    				self.$router.push({ path: '/FreeGenerate' })
				    			},2000);
			                }
			                else{
			                	Toast(result.error);
			                }
					  	})
					  	.catch(err => console.log(err))*/
					// 本地调试
					self.axios.post('/doLogin')
					  	.then(res => {

				    		var result = res.data;
				    		if(result.status == 1){
				    			Indicator.close();

				    			sessionStorage.setItem('accessToken', result.token);
				    			Toast({message:result.error || '登陆成功', duration: 1500});

				    			setTimeout(function(){
				    				self.$router.push({ path: '/FreeGenerate' })
				    			},2000);
			                }
			                else{
			                	Toast(result.error);
			                }
					  	})
					  	.catch(err => console.log(err))
				}
			},
			check(){
				if($checkAction.checkEmpty(this.mobile)){
					Toast('手机号不能为空');
					return false;
				}
				else if($checkAction.checkEmpty(this.card_num)){
					Toast('银行卡号不能为空');
					return false;
				}
				else{
					return true;
				}
			}
		}
	}
</script>
<style>
	.main{
		max-width: 750px;
		margin:0 auto;
	}
	.title{
		text-align: center;
		font-size: 30px;
	}
	.mint-button--normal{
		width: 100%;
	}
	.form-block{
		margin-top:35px;
	}
	.form-group{
		display: flex;
		margin-bottom: 30px;
		justify-content: center;
	}
	.form-input{
		position: absolute;
		border:0;
		top:0;
		left: 0;
		width: 100%;
		height: 100%;
		background:none;
		padding: 1px 10px;
		color: #fff;
	}
	.form-label{
		font-size: 16px;
		color: #fff;
	}
	.img-label{
		height: 17px;
		margin-top: 3px;
		margin-right: 10px;
	}
	.img-input{
		height: 24px;
	}
	.input-box{
		position: relative;
		height: 24px;
	}
	.btn{
		height: 110px;
		margin-bottom: 30px;
	}
	.btn img{
		height: 100%;
	}
	.img-banner{
		margin-top: 40px;
		width: 80%;
	}
</style>


