<template>
	<div class="main">
		<div class="p-text-box">
			<div class="p-text p-1">
				<img src="../assets/images/icon-word-7.png"><span>89%</span><img src="../assets/images/icon-word-6.png"><span>李小红</span><img src="../assets/images/icon-word-5.png">
			</div>
			<div class="p-text p-2">
				<img src="../assets/images/icon-word-4.png"><img src="../assets/images/icon-level-4.png"><img src="../assets/images/icon-word-3.png">
			</div>
		</div>
		<div class="cover-box">
			<img src="../assets/images/icon-line-1.png" style="margin-bottom:5px;">
			<div class="cover-img">
				<img src="" id="uploadCoverImg-image" />
			</div>
			<img src="../assets/images/icon-line-1.png" style="margin-top:5px;">
		</div>
		<div class="p-text-2">
			<img src="../assets/images/icon-level-4.png" class="img-level">
			<p>厉害了！我的土豪，咱们交个朋友吧！<br />好礼送不停，点击下方，参与活动。</p>
		</div>
		<div class="btn-share-img">
			<router-link :to="{path: '/Login'}"><img src="../assets/images/btn-5.png"></router-link>
		</div>
	</div>
</template>
<script>
 	import wx from 'weixin-js-sdk'
	import api from '../config/api'
	import '../assets/js/plupload/plupload.main.js'
	import { Toast, Indicator } from 'mint-ui'
	export default{
		created(){
		},
		mounted(){
			console.log(wx)
			this.get_file_fun("uploadCoverImg");
		},
		methods: {
			get_file_fun(name, callback){
		 		$("#"+name).ui_upload({
		            multi:false,
		            url: api.doUploadImage(),
		            FilesAdded:function(){
		                Indicator.open({
						  	text: '图片上传中...',
						  	spinnerType: 'fading-circle'
						});
		            },
		            FileUploaded:function(ajaxobj){
		                if(ajaxobj.state==200) {
		                    var img_url = ajaxobj.fullname;
		                    
		                    $("#"+name+"-url").val(img_url);
		                    $("#"+name+"-image").attr('src',img_url);

		                    if(typeof(callback) == 'function'){
		                        callback.call(this);
		                    }
		                }else{
		                    $.toast(ajaxobj.message);
		                }
		                Indicator.close();
		            },Error:function(error){
		            	Indicator.close();
		                if(error.code==-600){
		                    Toast("您上传的文件太大，最大允许上传1M");
		                }else{
		                    Toast(error.message);
		                }
		            }
		        });
			},
			bind_del_image(){
			 	$(".image_item").find(".remove_image").on("click",function() {
		            var self = $(this), img_url = self.attr("data-url");
		            del_image(self, img_url);
		            hide_imgupload();
		        });	
			},
			del_image(){
			 	$(o).parent().remove();
        		removeByValue(imgs, img_url);
			}
		}
	}
</script>
<style scoped>
	.p-text-box{
		margin-top:15px;
	}
	.p-text-box:after{
		content: ".";
	    display: block;
	    height: 0;
	    clear: both;
	    visibility: hidden
	}
	.btn-share-img{
		justify-content: center;
	}
	.p-text{
		text-align: center;
		margin-right: 0;
		height: 18px;
		line-height: 18px;
		float: right;
		margin-right: 5px;
		width: 100%;
	}
	.p-text *{
		float: right;
	}
	.cover-box{
		clear: both;
	}
</style>


