<template>
	<div class="main">
		<div class="btn-change-img" id="uploadCoverImg">
			<img src="../assets/images/btn-2.png" />
		</div>
		<div class="p-text p-1">
			<img src="../assets/images/icon-word-1.png">89%<img src="../assets/images/icon-word-2.png">
		</div>
		<div class="p-text p-2">
			<img src="../assets/images/icon-word-3.png"><img src="../assets/images/icon-level-4.png"><img src="../assets/images/icon-word-4.png">
		</div>
		<div class="cover-box">
			<img src="../assets/images/icon-line-1.png" style="margin-bottom:5px;">
			<div class="cover-img">
				<img src="" id="uploadCoverImg-image" />
			</div>
			<img src="../assets/images/icon-line-1.png" style="margin-top:5px;">
		</div>
		<div class="p-text-2">
			<img src="../assets/images/icon-level-4.png" class="img-level">
			<p>厉害了！我的土豪，咱们交个朋友吧！<br />好礼送不停，点击下方，参与活动。</p>
		</div>
		<div class="btn-share-img">
			<img src="../assets/images/btn-3.png">
			<img src="../assets/images/btn-4.png">
		</div>
	</div>
</template>
<script>
	import api from '../config/api';
	import '../assets/js/plupload/plupload.main.js';
	import { Toast, Indicator } from 'mint-ui';
	export default{
		created(){
		},
		mounted(){
			this.get_file_fun("uploadCoverImg");
		},
		methods: {
			get_file_fun(name, callback){
		 		$("#"+name).ui_upload({
		            multi:false,
		            url: api.doUploadImage(),
		            FilesAdded:function(){
		                Indicator.open({
						  	text: '图片上传中...',
						  	spinnerType: 'fading-circle'
						});
		            },
		            FileUploaded:function(ajaxobj){
		                if(ajaxobj.state==200) {
		                    var img_url = ajaxobj.fullname;
		                    
		                    $("#"+name+"-url").val(img_url);
		                    $("#"+name+"-image").attr('src',img_url);

		                    if(typeof(callback) == 'function'){
		                        callback.call(this);
		                    }
		                }else{
		                    $.toast(ajaxobj.message);
		                }
		                Indicator.close();
		            },Error:function(error){
		            	Indicator.close();
		                if(error.code==-600){
		                    Toast("您上传的文件太大，最大允许上传1M");
		                }else{
		                    Toast(error.message);
		                }
		            }
		        });
			},
			bind_del_image(){
			 	$(".image_item").find(".remove_image").on("click",function() {
		            var self = $(this), img_url = self.attr("data-url");
		            del_image(self, img_url);
		            hide_imgupload();
		        });	
			},
			del_image(){
			 	$(o).parent().remove();
        		removeByValue(imgs, img_url);
			}
		}
	}
</script>
<style>
	.btn-change-img{
		display: flex;
		justify-content: flex-end;
	}
	.btn-change-img img{
		height: 44px;
		margin-right: 36px;
		margin-top: 35px;
	}
	.p-text{
		font-size: 17px;
		font-family: 'Arial';
		text-align: right;
		color: #fffbc6;
		margin-right: 36px;
		margin-top: 15px;
		height: 17px;
		line-height: 17px;
		font-weight: 700;
	}
	.p-text img{
		height: 17px;
		margin: 0 1.5px;
	}
	.cover-box{
		margin:15px 15px 0;
	}
	.cover-img{
		margin: 0 5px;
		height: 59vw;
		background-color: #fff;
	}
	.cover-box img{
		display: block;
	}
	.p-text-2{
		text-align: left;
		margin-left: 36px;
		color: #fff;
		font-size: 14px;
		margin-top: 15px;
	}
	.p-text-2 .img-level{
		height: 17px;
	}
	.btn-share-img{
		text-align: left;
		margin:18px 36px;
		display: flex;
		justify-content: space-between;
	}
	.btn-share-img img{
		height: 44px;
	}
</style>


