const _baseUrl = process.env.API_ROOT
export default {
    doLogin() {
        return _baseUrl + '?ctl=user&act=login'
    },
 	getUserBills() {
        return _baseUrl + '?ctl=user&act=user_bills&itype=xr'
    },
    get_verifycode() {
        return _baseUrl + '?ctl=login&act=send_mobile_verify&itype=app'
    },
    get_weiboGoodsInfo() {
        return _baseUrl + '?ctl=weibo&act=goods_info&itype=xr'
    },
    doUploadImage(){
        return _baseUrl + '/m.php?m=PublicFile&a=do_upload&upload_type=1&dir=image';
    }
}



