<template>
  <div id="app">
    <div class="container" :style="{backgroundImage: 'url(' +page_bg + ')'}" >
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        page_bg: require('@/assets/images/bg-login.jpg')
      }
    }
  }
</script>
<style>
@import './assets/css/reset.css';
  img{max-width: 100%;}
  .container{
    position: absolute;
    width: 100%;
    min-height: 100%; 
    background-size:cover;
    background-repeat: no-repeat;
    text-align: center;
  }
  .fade-enter-active, .fade-leave-active {
      transition: all .4s ease;
  }
  .fade-enter{
      opacity:0;
  }
  .fade-leave-active{
      opacity:0;
  }
</style>



